html {
  height: 100%;
  font-size: 1em; }

body {
  font-size: 16px;
  line-height: 24px;
  background-color: whitesmoke;
  min-height: 100vh;
  min-width: 320px;
  overflow-x: hidden;
  font-family: 'Roboto', 'Helvetica', sans-serif;
  font-weight: 400; }

p {
  margin-top: 0;
  margin-bottom: 24px; }

b,
strong {
  font-weight: 700; }

ul,
ol {
  padding: 0;
  margin: 0; }

ul {
  list-style: none; }

li {
  padding: 0; }

a {
  color: inherit;
  outline: none; }

h6,
h5,
h4,
h3,
h2,
h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: inherit;
  box-sizing: border-box; }

h6 {
  font-size: 18px;
  line-height: 1.333; }

h5 {
  font-size: 20px;
  line-height: 1.2; }

h4 {
  font-size: 22px;
  line-height: 1.091; }

h3 {
  font-size: 24px;
  line-height: 1; }

h2 {
  font-size: 26px;
  line-height: 1.848; }

h1 {
  font-size: 28px;
  line-height: 1.718; }

small {
  display: block;
  font-size: 14px;
  line-height: 1.714;
  margin-bottom: 0; }

::selection {
  background-color: #fff;
  color: black; }

.hidden,
[hidden] {
  display: none !important;
  visibility: hidden !important; }

.hidden-visibility {
  visibility: hidden;
  opacity: 0; }

.breakline {
  display: block; }

.link {
  cursor: pointer;
  text-decoration: underline; }
  .link:disabled, .link[disabled] {
    cursor: not-allowed; }

.align-middle {
  display: inline-block;
  height: 100%;
  vertical-align: middle; }

.text-center {
  text-align: center; }

.overflow-hidden {
  overflow: hidden; }

.header {
  position: relative;
  flex: none;
  padding: 15px 0;
  box-sizing: border-box;
  z-index: 1; }
  .header-section {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .header-brand .icon {
    width: 48px;
    height: 48px;
    line-height: 48px;
    font-size: 40px;
    text-align: center;
    line-height: 1;
    box-sizing: border-box; }
  .header-profile {
    display: flex;
    align-items: center;
    justify-content: center; }
  .header .profile-info {
    flex: 1;
    color: black;
    padding: 0 10px;
    box-sizing: border-box; }
  .header .profile-title {
    text-transform: uppercase;
    text-align: right;
    font-size: 14px;
    line-height: 1; }
  .header .profile-category {
    font-size: 12px;
    line-height: 1;
    text-align: right; }
  .header .profile-avatar {
    flex: none;
    width: 48px;
    height: 48px;
    border-radius: 9999px;
    overflow: hidden; }
    .header .profile-avatar img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center; }

.panel-box {
  position: relative;
  max-width: 960px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr; }
  @media only screen and (max-width: 640px) {
    .panel-box {
      grid-template-columns: 1fr 1fr; } }
  @media only screen and (max-width: 480px) {
    .panel-box {
      grid-template-columns: 1fr; } }

.panel-item {
  padding: 10px;
  box-sizing: border-box; }
  @media only screen and (max-width: 480px) {
    .panel-item {
      margin: 0 auto;
      width: 100%;
      max-width: 320px; } }

.panel-item.compact {
  grid-column: 1 span; }

@media only screen and (min-width: 768px) {
  .panel-item.landscape {
    grid-column: 2 span; }
  .panel-item.square {
    grid-row: 2 span;
    grid-column: 2 / 2 span; }
  .panel-item.full {
    grid-column: 1 / -1; } }

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 16px;
  width: 100%;
  height: 100%;
  padding: 16px;
  background-color: rgba(255, 255, 255, 0.15);
  background-image: linear-gradient(90deg, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.15));
  background-clip: padding-box;
  backdrop-filter: blur(4px);
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.1);
  transition: all 400ms ease;
  overflow: hidden;
  box-sizing: border-box; }
  @media only screen and (prefers-color-scheme: dark) {
    .card {
      border: 1px solid rgba(255, 255, 255, 0.3);
      background-color: rgba(255, 255, 255, 0.15);
      background-image: linear-gradient(90deg, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.15)); } }
  .card:hover {
    background-color: rgba(255, 255, 255, 0.25); }
    @media only screen and (prefers-color-scheme: dark) {
      .card:hover {
        background-color: rgba(255, 255, 255, 0.25); } }
  .card::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(-50%, -50%);
    width: 60px;
    height: 60px;
    filter: blur(40px);
    background-color: white;
    opacity: .8;
    pointer-events: none;
    user-select: none;
    cursor: context-menu;
    z-index: 0; }
    @media only screen and (prefers-color-scheme: dark) {
      .card::before {
        background-color: white; } }
  .card-title {
    font-size: 16px;
    margin-bottom: 4px;
    text-align: center; }
  .card-label {
    font-size: 12px;
    line-height: 1;
    text-align: center;
    opacity: .8; }
  .card-links {
    margin-top: 16px;
    display: flex;
    justify-content: center; }
  .card .link {
    display: inline-block;
    text-decoration: none;
    margin: 0 4px; }
    .card .link:focus {
      outline: none; }
  .card .icon {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    width: 32px;
    height: 32px;
    border-radius: 8px;
    background-color: white;
    background-color: rgba(255, 255, 255, 0.6);
    box-sizing: border-box; }
    @media only screen and (prefers-color-scheme: dark) {
      .card .icon {
        background-color: rgba(255, 255, 255, 0.6); } }
  .card .icon.fa-external-link-alt {
    font-size: 14px; }

.app {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: blueviolet;
  background-image: url("../img/web-background.png");
  background-position: center center;
  background-size: cover; }

section {
  max-width: 1310px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;
  box-sizing: border-box; }

main {
  position: relative;
  padding: 0 15px;
  box-sizing: border-box;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1; }

.bubbles {
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  max-width: 1280px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  pointer-events: none;
  user-select: none;
  cursor: context-menu;
  z-index: 0; }

.bubble {
  display: block;
  position: absolute;
  width: 120px;
  height: 120px;
  pointer-events: none;
  user-select: none;
  cursor: context-menu;
  background-color: white;
  background-color: rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.4);
  animation: float 6s ease-in-out infinite;
  opacity: .2;
  border-radius: 9999px;
  z-index: 0; }
  .bubble-lg {
    width: 240px;
    height: 240px;
    animation: float-extend 6s ease-in-out infinite; }
  .bubble:nth-child(1) {
    animation-delay: -150ms; }
  .bubble:nth-child(2) {
    animation-delay: -300ms; }
  .bubble:nth-child(3) {
    animation-delay: -350ms; }
  .bubble:nth-child(4) {
    animation-delay: -50ms; }
  .bubble:nth-child(5) {
    animation-delay: -80ms; }

@keyframes float {
  0% {
    transform: translatey(0px); }
  50% {
    transform: translatey(-40px); }
  100% {
    transform: translatey(0px); } }

@keyframes float-extend {
  0% {
    transform: translatey(0px); }
  50% {
    transform: translatey(-20px); }
  100% {
    transform: translatey(0px); } }

article {
  flex: none;
  max-width: 640px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  color: black;
  padding-top: 40px; }
  @media only screen and (max-width: 640px) {
    article {
      padding-top: 20px;
      margin-bottom: 20px; } }
  article h1 {
    font-size: 18px;
    line-height: 1.2;
    margin-bottom: 8px;
    font-weight: bold;
    text-transform: uppercase; }
  article h2 {
    font-size: 14px;
    line-height: 1.2; }
